body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html{
  background-color: #282c34;
}

.App {
  text-align: center;
}

.App-logo {
  height: 30vmin;
}

.App-logo:hover, button {
  cursor: pointer;
}

.col {
  float: left;
  width: 33.33%;
}

/* Clear floats after the columns */
.row:after {
  content: "";
  display: table;
  clear: both;
}

.col {  
  display: flex;
  flex-direction: column;
  font-size: calc(10px + 2vmin);
  color: #999;
}

a, a:visited, a:active, .App-header {
  color: white;
  text-decoration: none;
}

a:hover {
  color: #61dbfb;
}

table{
  margin: 4em;
}

h3{
  margin-top: 4em;
  margin-left: 4em;
  margin-right: 4em;
}

.modal {
  margin-bottom: 4em;
}

th{
  padding-bottom: 2em;
}

.pelota{
  background-color: white;
  padding: .5em;
  border-radius: 50%;
  color: black;
  font-size: .5em;
}

img {
  padding-top: 3em;
}

button {
  background-color: #999;
  border: none;
  padding: 1em;
  border-radius: 5px;
  font-size: .5em;
}
